@import url(https://fonts.googleapis.com/css2?family=Varela+Round&display=swap);
*{box-sizing:border-box}body{margin:0;font-family:"Varela Round", sans-serif}.App{min-height:100vh;padding:10px 20px;font-size:30px}.App__title{margin-top:100px;text-align:left;font-size:30px}.App__list{display:flex;flex-direction:column;justify-content:start;grid-gap:10px;gap:10px;padding:0;margin:15px 0 0;list-style:none}.App__list--element{display:flex;align-items:center;padding:0 10px;border-radius:20px}.App__list--good{min-width:50px}.active{background-color:#c0e9a4}.button{font-family:"Varela Round", sans-serif;color:white;background-color:#ffb555;margin:10px  15px 10px auto;padding:5px 10px;border-radius:10px;border:3px solid #d89031;outline:none;cursor:pointer;transition:all 0.5s}.button:hover{background-color:#ff9913;border-color:#ac6100}.button__active{background-color:#ff9090;border-color:#d84531}.button__active:hover{background-color:#ff5353;border-color:#992313}.button__cleaner{background-color:white;border:none;border-radius:20px;font-size:50px;outline:none;position:absolute;left:700px;top:200px;transition:background-color 0.5s, border-color 0.5s}

